@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

.projects-section {
  min-height: 100vh;
  background-color: #fff;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Roboto', sans-serif;
}

.projects-heading {
  opacity: 0;
  font-size: 3rem;
  font-weight: 700;
  color: #1a1a1a;
  text-align: center;
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translate(-50%, 100px);
  z-index: 2;
  transition: all 1.5s cubic-bezier(0.16, 1, 0.3, 1);
}

.projects-heading.visible {
  opacity: 0.8;
  transform: translate(-50%, 0);
}

@media (max-width: 768px) {
  .projects-heading {
    font-size: 2.4rem;
  }
}

@media (max-width: 480px) {
  .projects-heading {
    font-size: 2rem;
  }
}

/* Responsive styles for Projects Page */

/* Tablet styles */
@media (max-width: 1024px) {
  .projects-section {
    padding: 2rem 0.5rem;
    min-height: unset;
    flex-direction: column;
  }
  .projects-heading {
    font-size: 2.1rem;
    top: 3%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, 0);
    padding: 0 1rem;
  }
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 1.2rem;
    width: 98%;
    margin-top: 6rem;
  }
  .project-card {
    padding: 1.2rem 1rem 1rem 1rem;
  }
}

/* Mobile styles */
@media (max-width: 600px) {
  .projects-section {
    padding: 0.5rem 0.2rem;
    min-height: unset;
    flex-direction: column;
  }
  .projects-heading {
    font-size: 1.2rem;
    top: 2%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, 0);
    padding: 0 0.5rem;
  }
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 0.7rem;
    width: 100%;
    margin-top: 4.5rem;
  }
  .project-card {
    padding: 0.8rem 0.5rem 0.7rem 0.5rem;
    border-radius: 12px;
  }
  .project-title {
    font-size: 1.1rem;
  }
  .project-desc {
    font-size: 0.95rem;
  }
  .tech-badge {
    font-size: 0.8rem;
    padding: 0.2em 0.7em;
  }
  .project-links {
    margin-top: 0.7rem;
  }
}

/* Extra small devices */
@media (max-width: 400px) {
  .projects-heading {
    font-size: 1rem;
    padding: 0 0.2rem;
  }
  .project-card {
    padding: 0.5rem 0.2rem 0.5rem 0.2rem;
  }
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  margin: 7rem auto 0 auto;
  width: 90%;
  max-width: 1100px;
  opacity: 0;
  transform: translateY(60px);
  transition: all 1.2s cubic-bezier(0.16, 1, 0.3, 1);
}

.projects-grid.visible {
  opacity: 1;
  transform: translateY(0);
}

.project-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 6px 32px rgba(31,38,135,0.10);
  padding: 2rem 1.5rem 1.5rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: box-shadow 0.3s;
}

.project-card:hover {
  box-shadow: 0 12px 36px rgba(31,38,135,0.18);
}

.project-title {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: #6C63FF;
}

.project-title:hover {
  background: linear-gradient(90deg, #6C63FF 0%, #48C6EF 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

.project-desc {
  font-size: 1rem;
  color: #444;
  margin-bottom: 1rem;
}

.project-tech {
  margin-bottom: 1rem;
}

.tech-badge {
  display: inline-block;
  background: #f3f3f3;
  color: #6C63FF;
  border-radius: 12px;
  padding: 0.3em 0.9em;
  font-size: 0.85rem;
  margin-right: 0.5em;
  margin-bottom: 0.3em;
  font-weight: 500;
}

.project-links {
  margin-top: auto;
}

.project-link {
  color: #6C63FF;
  font-weight: 500;
  margin-right: 1.2em;
  text-decoration: none;
  transition: color 0.2s, background 0.2s;
  background: none;
  -webkit-background-clip: unset;
  -webkit-text-fill-color: unset;
  background-clip: unset;
}

.project-link:hover {
  background: linear-gradient(90deg, #6C63FF 0%, #48C6EF 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

