.footer-gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background: linear-gradient(90deg, #6C63FF 0%, #48C6EF 100%);
  z-index: 1;
}

.footer-links a {
  background: linear-gradient(90deg, #6C63FF 0%, #48C6EF 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
  font-weight: 500;
  text-decoration: none;
  transition: opacity 0.2s;
}

.footer-links a:hover {
  opacity: 0.7;
}

